
import { Component, Prop, Vue, Watch } from 'vue-property-decorator';

import papa from 'papaparse';
import moment, { Moment } from 'moment';

import defaultData from './data';

moment.defaultFormat = 'DD/MM/YYYY HH:mm:ss';
moment.defaultFormatUtc = 'DD/MM/YYYY HH:mm:ss';

function readFile( file: File ): Promise<string> {

	return new Promise( ( resolve, reject ) => {
		const reader = new FileReader();
		reader.readAsText( file, 'UTF-8' );
		reader.onload = function re( evt ) {
			if ( evt.target?.result ) return resolve( evt.target.result as string );
			reject();
		};
		reader.onerror = function re( evt ) {
			reject();
		};
	} );
}


interface Original {
	central: string;
	unidad: string;
	emision: Moment;
	cambio: Moment;
	alcance: Moment;
	origen: string;
	tipo: string;
	base: string;
	run: boolean;
}


interface ProcceseedTime {
	central: string;
	unidad: string;
	cambio: Moment;
	tiempoDesconectado: number;
}



const runLike = [ 'CONECTAR UNIDAD', 'AUMENTAR CARGA', 'DISMINUIR CARGA', 'MANTENER CARGA', 'SINCRONIZAR UNIDAD' ];
const stopLike = [ 'DESCONECTAR UNIDAD', 'MANTENER PARO' ];




@Component
export default class ShowerComponent extends Vue {

	files: Array<File> = []
	rows: Array<Original> = [];
	tableContent: Array<ProcceseedTime> = []
	time = [ 'milliseconds', 'seconds', 'minutes', 'hours', 'days', 'weeks' ];
	selectedTime = 'hours';
	search = '';

	tableHeaders = [
		{ text: 'fecha', value: 'cambio' },
		{ text: 'central', value: 'central' },
		{ text: 'unidad', value: 'unidad' },
		{ text: 'tiempo desconectado', value: 'tiempoDesconectado' },
	]

	@Watch( 'selectedTime' )
	onSelectedTimeChange() {
		this.updateTableContent( this.rows );
	}


	updateTableContent( rows: Array<Original> ) {
		const b: Array<ProcceseedTime> = [];
		let lastOperationIndex = 0;
		rows.forEach( ( row, index ) => {
			if ( !row.run ) return;
			if ( rows[index - 1]?.run ) return lastOperationIndex = index;
			const block = rows.slice( lastOperationIndex + 1, index ).filter( ( r ) => r.unidad === row.unidad && r.central === row.central );
			const tiempoDesconectado = block.reduce( ( acumulator, r ) => acumulator += moment
				.duration( r.alcance.valueOf() - r.cambio.valueOf() ).as( this.selectedTime as any ), 0 );
			b.push( {
				central: row.central,
				unidad: row.unidad,
				cambio: row.cambio,
				tiempoDesconectado,
			} );
			lastOperationIndex = index;
		} );
		this.tableContent = b;
	}


	async procces() {
		let rows = ( await Promise.all( this.files.map( async ( file ) => {
			const text = await readFile( file );
			const data = papa.parse( text, { skipEmptyLines: true } )?.data;
			if ( !Array.isArray( data ) || data.length < 8 ) return;
			const FileData = data.splice( 8 ).map( ( row, index ) => {
				const emision = moment( row[2] + ' ' + row[3], 'DD/MM/YYYY HH:mm:ss' );
				const cambio = moment( row[4] + ' ' + row[5], 'DD/MM/YYYY HH:mm:ss' );
				const alcance = moment( row[6] + ' ' + row[7], 'DD/MM/YYYY HH:mm:ss' );

				return {
					central: row[0],
					unidad: row[1],
					emision,
					cambio,
					alcance,
					origen: row[8],
					tipo: row[9],
					base: row[10],
					run: runLike.includes( row[9] ),
				};

			} );
			return FileData;
		} ) ) ).flat() as Array<Original>;
		// rows = rows.sort( ( a, b ) => a.cambio.valueOf() - b.cambio.valueOf() );
		rows = rows.sort( ( a, b ) => a.unidad.localeCompare( b.unidad ) || a.cambio.valueOf() - b.cambio.valueOf() );
		this.rows = rows;
		// this.updateTableContent( rows );
		this.updateTableContent( rows );
	}


	async fakeProcces() {
		const text = defaultData;
		const data = papa.parse( text, { skipEmptyLines: true } )?.data;
		if ( !Array.isArray( data ) || data.length < 2 ) return;
		let FileData = data.splice( 1 ).map( ( row, index ) => ( {
			central: row[0],
			unidad: row[1],
			emision: moment( row[2], 'DD/MM/YYYY HH:mm:ss' ),
			cambio: moment( row[3], 'DD/MM/YYYY HH:mm:ss' ),
			alcance: moment( row[4], 'DD/MM/YYYY HH:mm:ss' ),
			origen: row[5],
			tipo: row[6],
			base: row[7],
			run: runLike.includes( row[6] ),
		} ) );

		// rows = rows.sort( ( a, b ) => a.cambio.valueOf() - b.cambio.valueOf() );
		FileData = FileData.sort( ( a, b ) => a.unidad.localeCompare( b.unidad ) || a.cambio.valueOf() - b.cambio.valueOf() );
		this.rows = FileData;
		// this.updateTableContent( rows );
		this.updateTableContent( FileData );
	}






	entradaToCsv( ) {
		if ( !this.rows || this.rows.length === 0 ) return;
		const fileContent = papa.unparse( this.rows.map( ( r ) => ( {
			...r,
			emision: r.emision.format(),
			cambio: r.cambio.format(),
			alcance: r.alcance.format(),
		} ) ) );
		if ( !fileContent ) return;
		this.download( fileContent, 'entrada' );
	}


	salidaToCsv() {
		if ( !this.tableContent || this.tableContent.length === 0 ) return;
		const fileContent = papa.unparse( this.tableContent );
		if ( !fileContent ) return;
		this.download( fileContent, 'salida' );
	}

	download( fileContent: string, name: string ) {
		const encodedUri = encodeURI( fileContent );
		const link = document.createElement( 'a' );
		link.setAttribute( 'href', 'data:text/csv;charset=utf-8,' + encodedUri );
		link.setAttribute( 'download', name + ' ' + moment().format( 'DD/MM/YYYY HH:mm' ) + '.csv' );
		document.body.appendChild( link ); // Required for FF
		link.click();
	}


	created() {
		this.fakeProcces();
	}



}
